@import "../../global";

.footer-wrapper {
  margin-top: 6vh;
  background-color: $color-grey;
  padding: 3rem 0;
  display: flex;
  flex-direction: column;
  .container {
    @media only screen and(max-width:144px) {
      width: 100%;
    }
    .logo {
        margin-top: 3rem;
        margin-bottom: 3rem;
        img{
            margin: 0;
            padding: 0;
            width: 148px;
            height: 24px;
        }
    }
    .footer-row {
      display: grid;
      grid-template-columns: 50% auto auto;
      grid-gap: 2rem;
      // justify-content: space-evenly;
      .footer-col {
        display: flex;
        flex-direction: column;
        p {
          line-height: 1.5rem;
          margin-bottom: 0.8rem;
          color: $text-dark;
        }
        h5 {
          font-size: 1rem;
          margin-bottom: 1rem;
        }
        a {
          color: $text-dark;
          text-decoration: none;
          line-height: 2rem;
          font-weight: 500;
          &:hover {
            color: $primary;
            font-weight: 600;
          }
        }
      }
    }
  }
}

@media only screen and (max-width: 1144px) {
  .footer-wrapper {
    padding: 2rem 0;
    .container {
      width: 100%;
      .logo {
          margin-bottom: 2rem;
        display: flex;
        img {
          position: relative;
        }
      }
    }
  }
}

@media only screen and (max-width: 768px) {
  .footer-wrapper {
    .container {
      .footer-row {
        grid-template-columns: auto auto;
      }
      .logo {
        margin-top: 0;
      }
    }
  }
}

@media only screen and (max-width: 500px) {
  .footer-wrapper {
      padding: 2rem 0;
    .container {
      .footer-row {
        grid-template-columns: auto;
      }
      .logo {
        img {
        }
      }
    }
  }
}
